import React from 'react'
import PropTypes from 'prop-types'

import { MenuItem } from '@blueprintjs/core'
import { Suggest } from '@blueprintjs/select'
import FormField from './FormField.js'

class SelectUsuario extends React.Component {

  constructor (props) {
    super()
   	this.state = {
   		usuario: props.selected
   	}
   	this.filterItems = this.filterItems.bind(this)
   	this.renderItem = this.renderItem.bind(this)
   	this.handleItemSelect = this.handleItemSelect.bind(this)  	
  }

  renderSelectedItem (item) {
  	if (!item) return ''
    const { nome, unidade, login } = item		
  	const txt = `${ nome } (${ unidade }) - ${ login }`
  	return txt
  }  

  renderItem (item, { modifiers, handleClick }) {
    if (!modifiers.matchesPredicate) {
      return null;
    }
  	const { nome, unidade, login } = item
    return (
      <MenuItem
        active={modifiers.active}
        key={ login }
        label={ login }
        text={ `${ nome } (${ unidade })` }
        onClick={ handleClick }
        shouldDismissPopover={false}
      />
    )
  }
  
  filterItems (searchStr) {
    searchStr = searchStr.toLowerCase()
  	return this.props.usuarios.filter(item => {
  		return item.nome.toLowerCase().indexOf(searchStr) >= 0 ||
  		       item.login.toLowerCase().indexOf(searchStr) >= 0 ||
  		       item.unidade.toLowerCase().indexOf(searchStr) >= 0
  	})
  }
  
  handleItemSelect (item) { 
  	this.setState({
  		usuario: item 
  	})
  	if (this.props.onChange)
  		this.props.onChange(item)
  }	

  render () {
    return (
			<FormField
	      message={this.props.message}
	      label={this.props.label || 'Usuário:'}
	    >
		    <Suggest
		      fill={true}
		      inputValueRenderer={ (item) => item }
		      items={this.props.usuarios}			
	        itemListPredicate={this.filterItems}		
		      itemRenderer={ this.renderItem }
		      onItemSelect={ this.handleItemSelect }
		      openOnKeyDown={true}
		      popoverProps={{ 
		      	minimal: true,
		      	position: 'bottom-right' 
		      }}
		      resetOnSelect={true}
		      selectedItem={ this.renderSelectedItem(this.props.onChange ? this.props.usuario : this.state.usuario) }
		    ></Suggest>  
		   </FormField>    	
    )
  }
}

SelectUsuario.propTypes = {
  label: PropTypes.string,
  message: PropTypes.string,
  onChange: PropTypes.func,
  selected: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.string
  ]),  
  usuarios: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.func
  ]).isRequired,
}

export default SelectUsuario





